import React, { Component } from "react";
import '../index.css';
class Square extends Component{
    constructor(props){
        super(props);
    }

    render(){
        if(this.props.active === 0){
            return (<div className = "square bg-light"> 

                    </div>);
        } else if(this.props.active === 1){
            return (<div className="bg-light"><div className = "square square-food bg-secondary"> </div></div>);
        }else {
            return (<div className = "bg-success square"> 

                    </div>);
        }
    }
}
export default Square;